header {
  font-family: "Montserrat";
}

body {
  min-width: 320px;
}

td {
  border: 1px solid black;
  width: 2rem;
  height: 2rem;
  font-size: 10px;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.config-hub {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  width: 100vw;
  background-color: #ccccff;
}

.config-hub-elements {
  background-color: #ccccff;
  color: black;
  overflow-x: auto;
  font-weight: bold;
  --hover-color: #d8b4ff;
}

.config-hub-elements:hover {
  background-color: #d8b4ff;
}

.visualizer-button-container {
  display: flex;
  justify-content: center;
}

.visualizer-button-container button {
  background-color: #e69a8dff;
  color: black;
  font-weight: 800;
  --hover-color: rgba(230, 154, 141, 0.8);
}

.visualizer-button-container button:hover,
.config-hub-elements:hover,
.clipboard-button:hover {
  font-weight: bold;
  background-color: var(--hover-color);
}

.grid-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  padding: 10px;
}

.grid-container table {
  border-collapse: collapse;
}

.wall-cell {
  background-color: #343a40;
}

.sand-cell {
  background-color: #d2b48c;
  color: transparent;
  --hover-color: white;
}

.water-cell {
  background-color: #000080;
  color: transparent;
  --hover-color: white;
}

.forest-cell {
  background-color: #006400;
  color: transparent;
  --hover-color: white;
}

.sand-cell:hover,
.forest-cell:hover,
.water-cell:hover {
  font-size: 10px;
  text-align: center;
  color: var(--hover-color);
}

.animate-weight-cell {
  color: transparent;
  background-color: #343a40;
  transform: scale(1.2);
  animation-duration: 0.5s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

.animate-path-cell {
  background: linear-gradient(#e6e6fa, #e6a8a8);
}

.path-cell {
  background-color: #e6a8a8;
}

.searched-cell {
  background-color: #e6e6fa;
}

.searched-cell,
.path-cell {
  font-weight: bold;
  text-align: center;
  color: black;
}

.waypoints {
  color: transparent;
  background-color: gold;
}

.settings {
  display: flex;
  flex-wrap: wrap;
}

.heuristic-grid,
.movement-grid,
.speed-grid,
.heuristic-weight-grid {
  padding: 5px;
}

.heuristic-button,
.speed-button,
.weight-slider {
  display: flex;
  flex-direction: row;
  padding: 2px;
}

.grid-clipboard {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  align-items: center;
}

.error-message {
  font-family: "Montserrat";
  font-weight: bold;
  font-size: 1.25em;
  color: #e83c3c;
}

.clipboard-button {
  height: 2rem;
  width: 8rem;
  border-radius: 2px;
  font-size: small;
  color: black;
  background-color: peachpuff;
  --hover-color: rgba(255, 218, 185, 0.8);
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.settings-icon {
  animation: rotate 2s linear infinite;
}

.algorithm-stats {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  font-family: "Inconsolata";
  font-weight: 300;
  height: 5rem;
  padding-left: 4px;
}

@media screen and (max-width: 1500px) {
  td {
    width: 1.6rem;
    height: 1.6rem;
  }
}

@media screen and (max-width: 1100px) {
  td {
    width: 1.5rem;
    height: 1.5rem;
  }
}

@media screen and (max-width: 830px) {
  td {
    width: 1.3rem;
    height: 1.3rem;
  }
}

@media screen and (max-width: 670px) {
  .config-hub-elements,
  .visualizer-button {
    font-size: 0.9375em;
  }

  .error-message {
    font-size: 0.7em;
  }

  td {
    width: 1rem;
    height: 1rem;
    font-size: 0.3125em;
  }
}

@media screen and (max-width: 440px) {
  .config-hub-elements,
  .visualizer-button {
    font-size: 0.625em;
  }

  .error-message {
    font-size: 0.625em;
  }

  .config-hub {
    height: 8rem;
  }

  td {
    width: 0.9rem;
    height: 0.9rem;
    font-size: 0.3125em;
  }
}

@media screen and (max-width: 320px) {
  .config-hub-elements,
  .visualizer-button {
    font-size: 0.3em;
  }

  .error-message {
    font-size: 0.3em;
  }

  .config-hub {
    width: 100vw;
    height: 10rem;
  }

  td {
    width: 0.1rem;
    height: 0.1rem;
    font-size: 0.1em;
  }
}
